.profile {
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  width: 400px;
  margin: 100px auto 50px auto;
  background-color: rgba(255, 255, 255, 0.616);
}

.description {
  align-items: center;
  flex-direction: column;
  display: flex;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  padding-top: 20px;
}

.avatar {
  display: block;
  height: 200px;
  width: 200px;
  background-color: #ccc;
  border-radius: 50%;
}

.name {
  margin: 0px;
  padding-top: 40px;
  font-size: 25px;
  font-weight: bold;
}

.tag {
  margin: 0px;
  padding: 15px 0px;
  font-size: 18px;
  font-weight: bold;
  color: rgba(61, 61, 61, 0.658);
}

.location {
  margin: 0px;
  padding-bottom: 40px;
  color: rgb(131, 130, 130);
  font-size: 18px;
  font-weight: bold;
  color: rgba(61, 61, 61, 0.658);
}

.stats {
  padding: 0px;
  margin: 0px;
  list-style: none;
  display: flex;
  /* justify-content: center; */
  justify-content: space-around;
  background-color: #ccc;
}

.label {
  font-size: 20px;
  font-weight: bold;
  color: rgba(61, 61, 61, 0.658);
  display: block;
  padding: 5px 0px;
}

.quantity {
  font-size: 20px;
  font-weight: 600;
  color: rgb(0, 0, 0);
  padding: 5px 0px;
  display: block;
}

.item {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 33.3%;
  height: 50px;
  padding: 50px 0px;
  border-right: 1px solid rgba(0, 0, 0, 0.15);
  background-color: #dbe9f5;
}

.item:not(:last-child) {
  border-right: 1px;
}
